/* eslint-disable no-useless-constructor */
import React,{Component, Fragment} from 'react';
import {connect} from 'react-redux'
import AlertWrapper from '../../../../components/alertItem/HighalertWrapper';
import AddBookHide from './AddBookHide';

import { actionCreators } from '../../store/index';
import {
    FormTitle,
    ItemNew,
    More


}from './style'
class AddBook extends Component{
   
   
    render(){
            const {showItem,changeHide,showAddBook}=this.props;
  
        return(
         
                <Fragment >
                     <FormTitle className="formTitle"></FormTitle> 
                    <form>
                         <ItemNew className="itemNew">        
                            <label>*</label><input type="number" required></input>
                         </ItemNew>
                         <ItemNew className="itemNew">
                            <label>*</label><input type="text" required></input>
                         </ItemNew>                         
                         <ItemNew className="itemNew">
                            <label>*</label><input type="text" placeholder="" required></input>
                         </ItemNew>    
                         <ItemNew className="itemNew ">
                            <label>*</label><input type="number" placeholder="" required></input>
                         </ItemNew>  
                         <ItemNew className="itemNew" >
                            <label>*</label>
                            <input type="radio" value="1" className="inputRadio"/>
                            <input type="radio" value="0" className="inputRadio" />
                            <input type="text" style={{width:"30%",opacity:"0"}}/>

                        </ItemNew>
                         <ItemNew className="itemNew " >
                            <label>*</label>
                            <input type="number" placeholder="" required></input>
                        </ItemNew>
                         <ItemNew className="itemNew detail" >
                            <label>*</label>
                            <textarea type="text" required ></textarea>
                        </ItemNew>
                        
                         {
                             showItem?<AddBookHide/>:(  <More className="more" onClick={changeHide}>
                                                                <span></span>  
                                                                <i className="iconfont jiantou">&#xe646;</i>
                                                         </More>  
                                                      )
                         }  
                         <button className="comfirm" onClick={this.props.submitBook}></button>  
                         <span className="cancel" onClick={showAddBook}></span>
                    </form>
                </Fragment>
            
        )
    }
}
const mapStateToProps=(state)=>({
    showItem:state.getIn(['manage','showItem'])

})
const mapDispatchToProps=(dispatch)=>({
      changeHide(){
          dispatch(actionCreators.changeHide());
      },
      showAddBook(){
          dispatch(actionCreators.showNew());
      },
      submitBook(){
          
      }

})

AddBook=connect(mapStateToProps,mapDispatchToProps)(AddBook);
export default AlertWrapper(AddBook);